.header {
  position: fixed;
  z-index: 10;
  display: flex;
  width: 100%;
  height: 70px;
  align-items: center;
  justify-content: space-between;
  padding: 0 10%;
  background-color: var(--color-main-background);
}

.logo img {
  width: 200px;
  object-fit: cover;
}

.iconBox {
  display: flex;
  align-items: center;
  justify-content: center;
}
